.nav-link.home {
    background-image: url("../media/a.jpg");
    
    margin-top: 60px;
    width: 100%;
    height: 600px;
    
  }
.topdiv{
    display: flex;
}

.intro_text {
    margin-top: 200px;

    float: left;
    margin-left: 180px;
    line-height: 0.8;
    color: white;

}

.home-img {
    width: 27%;
    box-shadow: 0 0 5px rgb(202, 50, 202), 0 0 10px rgb(143, 34, 143), 0 0 15px rgb(182, 28, 182), 0 0 20px rgb(218, 31, 218);
    float: right;
    border-radius: 500px;
    /* margin-right: 200px; */
    margin-top: 66px;
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 70%;
    transform: translate(-50%, -50%);
    transition: transform 0.2s ease-in-out;
    z-index: 0;
    /* position: relative; */
}

.home-img:hover {

    /* width: 30%; */
    transform: translate(-50%, -50%) scale(1.2);
}

.linkedin{
    width: 4%;
    background-color: white;
    padding: 10px;
     margin: 10px 10px;
     
    border-radius: 30px;
    cursor: pointer;
    
}
.linkedin:hover{
    box-shadow: 0 0 5px rgb(202, 50, 202), 0 0 10px rgb(143, 34, 143), 0 0 15px rgb(182, 28, 182), 0 0 20px rgb(218, 31, 218);
    transform: scale(1.1);
    transition: transform 0.3s ease;
}
.contact_box{
    margin-top: 10px;
     width:40%;
     display:flex;
     margin-left: 180px;

}
.intro_text h3{
    font-size: 32px;
}
.intro_text h1{
    font-size: 45px;
}
.intro_text h2{
    font-size: 42px;
    color: purple;
}



/* unicorn */
.unicorn{
    width: 5%;
}


@media only screen and (min-width:768px) and (max-width:1024px){
    .hombox {
        
       
        height: 500px;
        
      }

    .intro_text {
        margin-top: 180px;
        margin-left: 50px;
        float: inline-start;
    }

    .home-img {
        width: 25%;
        margin-top: 5px;
        
    }

    .linkedin{
        width: 7%;
        padding: 8px;
     
    }

    .contact_box{
       
         margin-left: 120px;
    
    }
    .intro_text h3{
        font-size: 20px;
    }
    .intro_text h1{
        font-size: 30px;
    }
    .intro_text h2{
        font-size: 30px;
    }
    
}

@media only screen and (max-width:768px) {

    .nav-link.home {
        display: flex;
        flex-direction: column;
        height: 400px;
        
      }
      
    

    .intro_text {
        margin-top: 200px;
        margin-left: 50px;
       
        
    }

    .home-img {
        width:35%;
        margin-top:-100px;
        margin-left: -70px;
        border-radius: 100px;
        
   
        
    }

    .linkedin{
        width: 15%;
        padding: 8px;
        
     
    }

    .contact_box{
       
         margin-left: 60px;
    
    }
    .intro_text h3{
        font-size: 15px;
    }
    .intro_text h1{
        font-size: 20px;
    }
    .intro_text h2{
        font-size: 20px;
    }

 
  
  
}
 

