.nav-menu {
    display: flex;

    gap: 50px;
    
    background-color: rgb(7, 7, 27);
    color: #d1d1d1;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    height: 65px;
    border: 1px solid purple;
    box-shadow: 0 0 5px purple, 0 0 10px purple, 0 0 15px purple, 0 0 20px purple, 0 0 40px purple, 0 0 50px purple, 0 0 80px purple;
    z-index: 1;
}
.toggle{
    display: none;
    
}



.my_logo img {
    width: 40%;
    margin-left: 20px;

}
.box{
    display: flex;
    gap: 50px;
    justify-content: flex-end;
    font-size: 15px;
}
.contents:hover{
    /* box-shadow: 0 0 5px rgb(202, 50, 202), 0 0 10px rgb(143, 34, 143), 0 0 15px rgb(182, 28, 182), 0 0 20px rgb(218, 31, 218); */
   color:purple;
   transform: scale(1.2);
    
}
.contents{
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    font-size: 22px;
}


.nav-link.resume {
    margin-right: 40px;
    padding: 5 8px;
    border: rgb(171, 16, 209) 2px solid;

    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    color: rgb(171, 16, 209);
    height: 60%;
    cursor: pointer;
    padding: 8px 8px;
    border-radius: 6px;
    transition: all 0.2s ease-in-out;

}
.nav-link.resume:hover{
    box-shadow: 0 0 5px rgb(202, 50, 202), 0 0 10px rgb(143, 34, 143); 
    transform: scale(1.1);
}

button span {
    display: inline-block;
    text-align: center;
    margin-right: 5px;
}

@media only screen and (min-width:768px) and (max-width:1024px){
    
    .my_logo img {
        width: 80%;
        
    }
    .toggle{
        display: none;
        
    }
    .box{
        
        font-size: 12px;
        gap:15px
    }
    .nav-link.resume {
        margin-right: 30px;
        padding: 4 5px;
       
    }
}

@media only screen and (max-width:768px) {
.box{
    display: none;
    /* color: rgb(7, 7, 27); */

}
.my_logo img{
    width:100px;
    height: 30px;
    margin-left: -18px;
    margin-right: -18px;
}
.toggle{
    
    position: relative;
    display: inline-block;
    line-height:0.5;
}


  .dropdown_cont {
    display: none;
    position: absolute;
    background-color: transparent;
    min-width: 100px;
   
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .dropdown_cont h3 {
    color: rgb(158, 154, 154);
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    font-size: 12px;
  }
  
  .dropdown_cont h3:hover {background-color: #ddd;}
  
  .toggle:hover .dropdown_cont {display: block;}
  
  
  
}

