.contact-phone{
  width:2%;
  height: 20px;
  background-color: white;
  display: inline-block;
 padding: 15px 25px;
 border-radius: 5px;
 margin: 20px;
 margin-bottom: -15px;
cursor: pointer;
}
.contact-phone:hover{
  box-shadow: 0 0 5px purple, 0 0 10px purple, 0 0 15px purple, 0 0 20px purple, 0 0 40px purple, 0 0 50px purple, 0 0 80px purple;

}

